import React, { useState, useEffect } from "react";
import StatesDropDown from "./dropdown";
import BarChartGraph from "./barchart";

const StateWiseGraph = (props) => {
  const [selectedState, setState] = useState("Select State");
  const [stateData, setStateData] = useState([]);
  const [index, setIndex] = useState({
    startIndex: 0,
    endIndex: 10,
  });

  function selectHandler(e) {
    setState(e);
    setData(e, props.stateWiseGraphData);
  }

  function setData(state, rawData) {
    let data = rawData.filter((d) => d.state === state);
    setStateData(data[0].statesData);
    setIndex({
      startIndex: parseInt(data[0].statesData.length - 10),
      endIndex: parseInt(data[0].statesData.length - 1),
    });
  }

  useEffect(() => {
    if (props.stateWiseGraphData.length > 0) {
      setState("Karnataka");
      setData("Karnataka", props.stateWiseGraphData);
    }
  }, [props.stateWiseGraphData]);

  return (
    <div>
      <StatesDropDown
        selectHandler={selectHandler}
        selectedState={selectedState}
        stateNames={props.stateNames}
      />
      {stateData && <BarChartGraph index={index} stateData={stateData} />}
    </div>
  );
};
export default StateWiseGraph;
