import React, { PureComponent } from "react";
import {
  BarChart,
  Brush,
  ResponsiveContainer,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
} from "recharts";
import { statesNames } from "../../../constants";

class CustomizedLabel extends PureComponent {
  render() {
    const { x, y, stroke, value } = this.props;

    return (
      <text x={x} y={y} dy={-4} fill={stroke} fontSize={10} textAnchor="middle">
        {value}
      </text>
    );
  }
}

class CustomizedAxisTick extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;

    return (
      <g transform={`translate(${x},${y})`}>
        <text
          x={0}
          y={0}
          dy={3}
          textAnchor="end"
          fill="#666"
          transform="rotate(-90)"
          fontSize="12px"
        >
          {statesNames.map(
            (state) => state.name === payload.value && state.shortcut
          )}
        </text>
      </g>
    );
  }
}
export default class BarChartGraph extends PureComponent {
  state = {
    data: this.props.states,
  };
  componentDidMount() {
    let data = this.state.data;
    data = data.filter(
      (d) =>
        !(
          d.active == 0 &&
          d.confirmed == 0 &&
          d.deaths == 0 &&
          d.recovered == 0
        )
    );

    this.setState({ data: data.slice(1) });
  }
  render() {
    return (
      <div style={{ width: "100%", height: 400 }}>
        <ResponsiveContainer>
          <BarChart
            width={500}
            height={400}
            data={this.state.data}
            margin={{
              top: 20,
              right: 30,
              left: -10,
              bottom: 10,
            }}
          >
            {/* <CartesianGrid strokeDasharray="1 1" /> */}
            <XAxis
              interval={0}
              dataKey="state"
              height={40}
              tick={<CustomizedAxisTick />}
            />
            <YAxis />
            <Tooltip contentStyle={{ fontSize: "14px" }} />
            <Legend
              layout="horizontal"
              verticalAlign="bottom"
              align="center"
              wrapperStyle={{
                left: "1rem",
                textAlign: "center",
              }}
            />
            <Bar dataKey="confirmed" stackId="a" fill="#8884d8" />
            <Bar dataKey="active" stackId="a" fill="#82ca9d" />
            <Bar dataKey="deaths" stackId="a" fill="red" />
            <Bar dataKey="recovered" stackId="a" fill="green" />
            <Brush
              dataKey="state"
              height={30}
              stroke="#000000"
              startIndex={0}
              endIndex={10}
            >
              <BarChart>
                <Bar dataKey="active" barSize={20} fill="#82ca9d" />
              </BarChart>
            </Brush>
          </BarChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
