import React, { useState } from "react";
import { Dropdown } from "react-bootstrap";

const StatesDropDown = (props) => {
  return (
    <Dropdown onSelect={props.selectHandler}>
      <Dropdown.Toggle
        variant="success"
        id="dropdown-basic"
        style={{ background: "white", color: "black" }}
      >
        {props.selectedState}
      </Dropdown.Toggle>

      <Dropdown.Menu style={{ height: "250px", overflow: "scroll" }}>
        {props.stateNames &&
          props.stateNames.map((state, index) => (
            <Dropdown.Item
              key={index}
              eventKey={`${state}`}
              active={state === props.selectedState}
            >
              {state}
            </Dropdown.Item>
          ))}
      </Dropdown.Menu>
    </Dropdown>
  );
};
export default StatesDropDown;
