import React, { useState, useEffect } from "react";
import style from "./style.css";
import axios from "axios";
import RenderLineChart from "../charts/linechart";
import BarChartGraph from "../charts/barchart";
import ChoroplethMap from "../charts/map/choropleth";
import { Container, Row, Col } from "react-bootstrap";
import Stats from "../stats";
import HelpContacts from "../help";
import { getStates, getStatewiseGraphData } from "../api";
import StateWiseGraph from "../charts/statewisechart";

const DashBoard = (props) => {
  const [states, setStates] = useState([]);
  const [stateNames, setStateNames] = useState([]);
  const [stateWiseGraphData, setStateData] = useState([]);
  const [total, setTotal] = useState(0);
  const [fetched, setFetched] = useState(false);
  const [contacts, setContacts] = useState([]);
  const [lastUpdated, setLastUpdated] = useState("");
  const [timeseries, setTimeseries] = useState([]);
  const [deltas, setDeltas] = useState([]);
  useEffect(() => {
    if (fetched === false) {
      fetchStates();
      getHelpDetails();
    }
  }, [fetched]);

  const getHelpDetails = () => {
    axios
      .get("https://api.rootnet.in/covid19-in/contacts")
      .then((response) => {
        setContacts(response.data.data.contacts);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const fetchStates = () => {
    getStates()
      .then((response) => {
        setTotal(response.statewise[0].confirmed);
        setStates(response.statewise);
        setTimeseries(response.cases_time_series);
        setLastUpdated(
          response.statewise[0].lastupdatedtime.slice(0, 15) +
            response.statewise[0].lastupdatedtime.slice(18)
        );
        setDeltas(response.key_values[0]);
        setFetched(true);

        //to fetch statewise
        getStatewiseGraphData().then((data) => {
          let states = response.statewise.map((d) => d.state).slice(1);
          setStateNames(states);
          let getStateData = (state) => {
            return data.map((d) =>
              Object.assign(
                {},
                {
                  day: d.day,
                  ...d.regional.filter(
                    (d) =>
                      d.loc === (state === "Telangana" ? "Telengana" : state)
                  )?.[0],
                }
              )
            );
          };
          setStateData(
            states.map((state) => {
              return Object.assign(
                {},
                {
                  state: state,
                  statesData: getStateData(state),
                }
              );
            })
          );
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <Container className="dashboard">
      <span>Last updated: {lastUpdated}</span>
      <Stats data={states} deltas={deltas} />
      <Row>
        <Col xs={12} sm={12} md={6} lg={6}>
          {timeseries.length > 0 ? (
            <div className="card">
              <RenderLineChart timeseries={timeseries} total={total} />
            </div>
          ) : (
            "Loading..."
          )}
        </Col>
        <Col xs={12} sm={12} md={6} lg={6}>
          {states.length > 0 ? (
            <div className="card">
              <BarChartGraph states={states} />
            </div>
          ) : (
            "Loading..."
          )}
        </Col>
      </Row>

      <Row>
        <ChoroplethMap states={states} deltas={deltas} />
      </Row>
      <Row>
        <Col>
          {stateNames.length > 0 && stateWiseGraphData.length > 0 ? (
            <div className="card">
              <StateWiseGraph
                stateNames={stateNames}
                stateWiseGraphData={stateWiseGraphData}
              />
            </div>
          ) : (
            "Loading..."
          )}
        </Col>
      </Row>
      <Row>
        <Col>
          <HelpContacts contacts={contacts} />
        </Col>
      </Row>
    </Container>
  );
};

export default DashBoard;
