import React, { PureComponent } from "react";
import {
  BarChart,
  Brush,
  ResponsiveContainer,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
} from "recharts";
import { statesNames } from "../../../constants";

class CustomizedLabel extends PureComponent {
  render() {
    const { x, y, stroke, value } = this.props;

    return (
      <text x={x} y={y} dy={-4} fill={stroke} fontSize={10} textAnchor="middle">
        {value}
      </text>
    );
  }
}

class CustomizedAxisTick extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;

    return (
      <g transform={`translate(${x},${y})`}>
        <text
          x={0}
          y={0}
          dy={3}
          textAnchor="end"
          fill="#666"
          transform="rotate(-90)"
          fontSize="12px"
        >
          {statesNames.map(
            (state) => state.name === payload.value && state.shortcut
          )}
        </text>
      </g>
    );
  }
}
export default class BarChartGraph extends PureComponent {
  render() {
    return (
      <div style={{ width: "100%", height: 400 }}>
        <ResponsiveContainer>
          <BarChart
            width={500}
            height={400}
            data={this.props.stateData}
            margin={{
              top: 20,
              right: 30,
              left: -10,
              bottom: 10,
            }}
          >
            {/* <CartesianGrid strokeDasharray="1 1" /> */}
            <XAxis
              dataKey="day"
              height={40}
              wrapperStyle={{}}
              tickFormatter={(d) => {
                let date = new Date(d);
                return (
                  date.getDate() +
                  " " +
                  date.toLocaleString("default", {
                    month: "short",
                  })
                );
              }}
            />
            <YAxis />
            <Tooltip contentStyle={{ fontSize: "14px" }} />
            <Legend
              layout="horizontal"
              verticalAlign="bottom"
              align="center"
              wrapperStyle={{
                left: "1rem",
                textAlign: "center",
              }}
            />
            <Bar dataKey="confirmedCasesIndian" stackId="a" fill="#8884d8" />
            <Bar dataKey="confirmedCasesForeign" stackId="a" fill="#82ca9d" />
            <Bar dataKey="deaths" stackId="a" fill="red" />
            <Bar dataKey="discharged" stackId="a" fill="green" />
            <Brush
              dataKey="day"
              height={30}
              stroke="#000000"
              startIndex={this.props.index.startIndex}
              endIndex={this.props.index.endIndex}
            >
              <BarChart>
                <Bar
                  dataKey="confirmedCasesIndian"
                  barSize={20}
                  fill="#82ca9d"
                />
              </BarChart>
            </Brush>
          </BarChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
